code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(0, 0, 8);
  padding-top: 50px;
  color: white;
  text-align: center;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.Cell {
  -webkit-animation: fadeIn 300ms ease-in-out;
          animation: fadeIn 300ms ease-in-out;
  height: 100px;
  width: 100px;
  background-color: rgb(9, 52, 71);
  border-radius: 2px;
  cursor: pointer;
}

.Cell-lit {
  background-color: rgb(16, 94, 128);
}
.Board {
  margin: 0 auto;
}

button {
  background-color: rgb(12, 67, 247);
  border: none;
  color: white;
  font-size: 2rem;
  margin-top: 2rem;
  padding: 0.5rem 1rem;
}

.container {
  margin-top: 5rem;
}

.neon-win-orange {
  font-family: neon;
  color: #FB4264;
  font-size: 150px;
  line-height: 9vw;
  text-shadow: 0 0 3vw #F40A35;
  animation: neon-win-orange 1s ease infinite;
  -moz-animation: neon-win-orange 1s ease infinite;
  -webkit-animation: neon-win-orange 1s ease infinite;
}

.neon-win-blue {
  font-family: neon;
  color: #426DFB;
  font-size: 150px;
  line-height: 9vw;
  text-shadow: 0 0 3vw #2356FF;
  animation: neon-win-blue 2s linear infinite;
  -moz-animation: neon-win-blue 2s linear infinite;
  -webkit-animation: neon-win-blue 2s linear infinite;
  -o-animation: neon-win-blue 2s linear infinite;
}

@-webkit-keyframes neon-win-orange {

  0%,
  100% {
    text-shadow: 0 0 1vw #FA1C16, 0 0 3vw #FA1C16, 0 0 10vw #FA1C16, 0 0 10vw #FA1C16, 0 0 .4vw #FED128, .5vw .5vw .1vw #806914;
    color: #FED128;
  }

  50% {
    text-shadow: 0 0 .5vw #800E0B, 0 0 1.5vw #800E0B, 0 0 5vw #800E0B, 0 0 5vw #800E0B, 0 0 .2vw #800E0B, .5vw .5vw .1vw #40340A;
    color: #806914;
  }
}

@keyframes neon-win-orange {

  0%,
  100% {
    text-shadow: 0 0 1vw #FA1C16, 0 0 3vw #FA1C16, 0 0 10vw #FA1C16, 0 0 10vw #FA1C16, 0 0 .4vw #FED128, .5vw .5vw .1vw #806914;
    color: #FED128;
  }

  50% {
    text-shadow: 0 0 .5vw #800E0B, 0 0 1.5vw #800E0B, 0 0 5vw #800E0B, 0 0 5vw #800E0B, 0 0 .2vw #800E0B, .5vw .5vw .1vw #40340A;
    color: #806914;
  }
}

@-webkit-keyframes neon-win-blue {

  0%,
  100% {
    text-shadow: 0 0 1vw #1041FF, 0 0 3vw #1041FF, 0 0 10vw #1041FF, 0 0 10vw #1041FF, 0 0 .4vw #8BFDFE, .5vw .5vw .1vw #147280;
    color: #28D7FE;
  }

  50% {
    text-shadow: 0 0 .5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180, 0 0 .2vw #082180, .5vw .5vw .1vw #0A3940;
    color: #146C80;
  }
}

@keyframes neon-win-blue {

  0%,
  100% {
    text-shadow: 0 0 1vw #1041FF, 0 0 3vw #1041FF, 0 0 10vw #1041FF, 0 0 10vw #1041FF, 0 0 .4vw #8BFDFE, .5vw .5vw .1vw #147280;
    color: #28D7FE;
  }

  50% {
    text-shadow: 0 0 .5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180, 0 0 .2vw #082180, .5vw .5vw .1vw #0A3940;
    color: #146C80;
  }
}
